import { useRef, useState } from "react";



const Aboutme = () => {
    const [t ,f ] = useState(true)
    let imagefil = useRef(null);


    const mydun = ()=>{
        if(t){
            imagefil.current.style.filter ="blur(0px)";
            f(false)
        }else{
            imagefil.current.style.filter ="blur(20px)";
            f(true)

        }
       
    }
    

    return (
        <section className="Aboutme" >
            <h1>About Me<span className="spanh1"></span></h1>

            <div className="master_about">
                <div className="first">

                    <div className="code">
                        <span className="slace" >&#123; </span>
                        <div className="div_c" ><h2><span className="c_s_s" >"</span>name  <span className="c_s_s" > " : </span> <span className="code_C"  >"DILSHAD ANSARI"</span><span className="c_s_s" >,</span></h2></div>
                        <div className="div_c" ><h2><span className="c_s_s" >"</span>age  <span className="c_s_s" > " : </span> <span className="code_C"  >"20"</span><span className="c_s_s" >,</span></h2></div>
                        <div className="div_c" ><h2><span className="c_s_s" >"</span>collage  <span className="c_s_s" > " : </span> <span className="code_C"  >"nillamber pitamber university"</span><span className="c_s_s" >,</span></h2></div>
                        <div className="div_c" ><h2><span className="c_s_s" >"</span>course  <span className="c_s_s" > " : </span> <span className="code_C"  >"React js developer"</span><span className="c_s_s" >,</span></h2></div>
                        <div className="div_c" ><h2><span className="c_s_s" >"</span>status<span className="c_s_s" > ": </span> <span className="code_C"  >"version updated 0.2"</span></h2></div>
                        <span className="slace" >&#125;</span>
                    </div>



                </div>
                <div className="secand">
                    <img src="img/mypic.jpg" alt="developerpic here" ref={imagefil} />
                </div>
            </div>
            <button  className="btn" onClick={mydun} >Run Code</button>
        </section>
    )
}

export default Aboutme;