import swal from "sweetalert";
import { Watch } from "react-loader-spinner";
import { useState } from "react";
import { databse , storage , app } from "./Firebase/Firebase";
import { addDoc, collection } from "firebase/firestore";




const RequestCallFrom = () => {

   

 const  collectionRef = collection(databse , "user")
  const [leader, setloeader] = useState(false);
  const [data, setdata] = useState({
    name: "",
    email: "",
    number: "",
    msg: ""
  });



  const Mychange = (e) => {
    if (e.target.name === "name") {
      setdata({ name: e.target.value })
    } else if (e.target.name === "email") {
      setdata({ ...data, email: e.target.value })
    } else if (e.target.name === "number") {
      setdata({ ...data, number: e.target.value })
    } else {
      setdata({ ...data, msg: e.target.value })
    }
  }



  const Mysubmite = (e) => {
    e.preventDefault();
    setloeader(true)
    console.log(data);

    addDoc(collectionRef, {
      name: data.name,
      email: data.email,
      number: data.number,
      msg:data.number
    }).then((done) => {
      swal({
        title: "Thanks ",
        icon: "success",
        buttons: false,
        timer: 3000
      });
      setloeader(false)
      
    }).catch((eroor) => {
      swal({
        title: eroor.message,
        icon: "error",
        buttons: false,
        timer: 3000
      });
      setloeader(false)

    })






  }




  return (
    <div style={{ display: "flex", justifyContent: "center", alignItems: "center", width: "100%", height: "100%", backgroundColor: "#03111e" }}>

      <div className="container dilshad" style={{ width: "600px" }} >
        <form id="contact" onSubmit={Mysubmite}>
          <h3 style={{ textAlign: "center", marginBottom: "20px" }} >Request for Call Back</h3>

          <fieldset className="forms"  >
            <input value={data.name} onChange={Mychange} name="name" placeholder="Your name" type="text" tabIndex="1" required  />
          </fieldset>
          <fieldset>
            <input value={data.email} onChange={Mychange} name="email" placeholder="Your Email Address (optional) " type="email" tabIndex="2" />
          </fieldset>
          <fieldset>
            <input value={data.number} onChange={Mychange} name="number" placeholder="Your Phone Number" type="tel" tabIndex="3" required />
          </fieldset>
          <fieldset>
            <textarea placeholder="Type your message here...." tabIndex="5" required value={data.msg} onChange={Mychange} name="msg"  ></textarea>
          </fieldset>
          <fieldset>
            <button name="submit" type="submit" onSubmit={Mysubmite} >{leader ? <div className="loeader" > <Watch width={30} height={30} color="white" /> </div> : "Submite"}</button>

          </fieldset>
        </form>
      </div>

    </div>
  )
}

export default RequestCallFrom;